import { Component, Input, OnInit } from '@angular/core';
import {
  CounterPartyDetails,
  PaymentTypeConfig,
} from '../../../configs/models/form-items.model';
import { BeneficiaryDetailsTitle } from '../../constants';
import { counterpartyI18n } from '../../constants/counterparty.i18n';
import { isAddressTruthy } from '../../helpers/type-helpers';
import { Address } from '../../models/types';

@Component({
  selector: 'bb-payord-counterparty-details',
  templateUrl: './counterparty-details.component.html',
})
export class PayordCounterpartyDetailsComponent implements OnInit {
  /**
   * The config of current payment type
   */
  @Input() config!: PaymentTypeConfig;
  /**
   * The details of beneficiary
   */
  @Input() details!: CounterPartyDetails;
  /**
   * Flag to denote debit payment
   */
  @Input() isDebitPayment = false;
  beneficiaryDetailsTitles?: BeneficiaryDetailsTitle;

  hasPostalAddress(address?: Address): boolean {
    return isAddressTruthy(address);
  }

  ngOnInit() {
    this.beneficiaryDetailsTitles = this.isDebitPayment
      ? {
          beneficiaryTitle: counterpartyI18n.beneficiaryDebitTitle,
          bankTitle: counterpartyI18n.beneficiaryBankDebitTitle,
          addressTitle: counterpartyI18n.beneficiaryAddressDebitTitle,
        }
      : {
          beneficiaryTitle: counterpartyI18n.beneficiaryTitle,
          bankTitle: counterpartyI18n.beneficiaryBankTitle,
          addressTitle: counterpartyI18n.beneficiaryAddressTitle,
        };
  }
}
